import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import React from 'react';
import { Button } from '../stories/Button';



interface ModalProps{
  titleModal:string;
  titleButon:string;
  description:string;
}
export const Modal=({
  titleModal='Modal Diplomado',
  titleButon='Modal example',
  description='esto es un ejemplo de implementacion del modal'
}:ModalProps)=>{
  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };
  return (
    <div>
      <Button size="medium" onClick={handleClickOpen} label={titleButon}/>

      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">
          {titleModal}
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
          {description}
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} label="Disagree"/>
          <Button onClick={handleClose} label="Agree"/>
        </DialogActions>
      </Dialog>
    </div>
  );

};


